import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
//import { AppComponent } from './app.component';
//import { PageIntroComponent } from './page-intro/page-intro.component';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { AuthGuardService } from './shared/guards/auth-guard.service';
import { UserSurveyComponent } from './user-survey/user-survey.component';


const routes: Routes = [
  { path: 'register', component: RegisterComponent },
  { path: 'login', component: LoginComponent },
  { path: 'surveys', component: UserSurveyComponent },
  { path: '', loadChildren: () => import('./customer/customer.module').then(m => m.CustomerModule) ,canActivate: [AuthGuardService]},
  { path: '**', redirectTo: '' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
