import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json',
    // 'Authorization': 'my-auth-token'
  })
};
@Injectable({
  providedIn: 'root'
})
export class SurveyService {

  baseUrl = environment.apiEndpoint + '/api/';

  constructor(private http: HttpClient) { }

  public getAll = (botId: any, blockId: any) => {
    return this.http.get(this.baseUrl + 'Ai/Bots/' + botId + '/Survey');
  }
  public getById = (botId: any, surveyId: any) => {
    return this.http.get(this.baseUrl + 'Ai/Bots/' + botId + '/Survey/' + surveyId);
  }
  public getIsDesign = (botId: any) => {
    return this.http.get(this.baseUrl + 'Ai/Bots/' + botId + '/SurveyIsDesign');
  }

  public getIsUser = (botId: any) => {
    return this.http.get(this.baseUrl + 'Ai/Bots/' + botId + '/SurveyIsUser');
  }

  public search = (botId: any, data: any) => this.http.post<any>(this.baseUrl + 'Ai/Bots/' + botId + '/SurveyIsUser/Search', data, httpOptions);
  // tslint:disable-next-line: max-line-length
  public create = (botId: any, data: any) => this.http.post<any>(this.baseUrl + 'Ai/Bots/' + botId + '/Survey', data, httpOptions);
  public createForm = (botId: any, data: any) => this.http.post<any>(this.baseUrl + 'Ai/Bots/' + botId + '/SurveyForm', data, httpOptions);
  public update = (botId: any, surveyId: any, data: any) => this.http.put<any>(this.baseUrl + 'Ai/Bots/' + botId + '/Survey/' + surveyId, data, httpOptions);
  public removeById = (botId: any, surveyId: any) => {
    return this.http.delete<any>(this.baseUrl + 'Ai/Bots/' + botId + '/Survey/' + surveyId);
  }
}
