import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators, FormGroupDirective, NgForm } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthService } from '../shared/services/auth.service';
import * as CryptoJS from 'crypto-js';
import { ErrorStateMatcher } from '@angular/material/core';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  hide = true;
  isLoading = false;
  loginForm = new FormGroup({
    'fullName': new FormControl('', [Validators.required]),
    'userName': new FormControl('', [Validators.required]),
    'phone': new FormControl('', [Validators.required]),
    'pass': new FormControl('', [Validators.required, Validators.minLength(8)]),
    'accept':  new FormControl(false,[Validators.required]),
    'confirmPassword': new FormControl('', [Validators.required]),

  }, { validators: this.checkPasswords });
  returnUrl: string;
  get userName() { return this.loginForm.get('userName'); }
  get pass() { return this.loginForm.get('pass'); }
  get phone() { return this.loginForm.get('phone'); }
  get fullName() { return this.loginForm.get('fullName'); }
  get accept() { return this.loginForm.get('accept'); }
  get confirmPassword() { return this.loginForm.get('confirmPassword'); }

  matcher = new MyErrorStateMatcher();
  checkPasswords(group: FormGroup) { // here we have the 'passwords' group
    let password = group.get('pass').value;
    let confirmPassword = group.get('confirmPassword').value;

    return password === confirmPassword ? null : { notSame: true }
  }

  constructor(
    private authService: AuthService,
    private _snackBar: MatSnackBar,
    private route: ActivatedRoute,
    private router: Router,


  ) {
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
    if (this.authService.isAuthenticated()) {
      this.router.navigate([this.returnUrl]);
    }
  }

  ngOnInit(): void {

  }


  onSubmit() {
    let data = {
      fullName: this.loginForm.value.fullName,
      userName: this.loginForm.value.userName,
      phone: this.loginForm.value.phone,
      pass: this.loginForm.value.pass
    }
    //console.log(data)
    this.isLoading = true;
    this.authService.register(data).subscribe(
      res => {

        setTimeout(() => {
          this.isLoading = false;
        }, 500);
        if (res != null) {
          this._snackBar.open('Đăng ký thành công', 'OK', {
            duration: 1000
          });
          this.router.navigate(['/login']);
        }
        else
          this._snackBar.open('Đăng ký không thành công', 'OK', {
            duration: 1000
          });
      },
      error => {
        //console.log(error)
        setTimeout(() => {
          this.isLoading = false;
        }, 500);

        this._snackBar.open('Đăng ký không thành công', 'OK', {
          duration: 1000
        });
      }

    )
  }
}

export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const invalidCtrl = !!(control?.invalid && control?.parent?.dirty);
    const invalidParent = !!(control?.parent?.invalid && control?.parent?.dirty);

    return invalidCtrl || invalidParent;
  }
}