import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { AppRoutingModule } from './app-routing.module';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatSnackBarModule } from '@angular/material/snack-bar';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatCardModule } from '@angular/material/card';
import { MatInputModule } from '@angular/material/input';
import { MatButtonModule } from '@angular/material/button';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatSelectModule } from '@angular/material/select';

import { SocketioService } from './shared/services/socketio.service';
import { UserService } from './shared/services/user.service';
import { AuthGuardService } from './shared/guards/auth-guard.service';
import { AuthService } from './shared/services/auth.service';
import { AppComponent } from './app.component';
import { LoginComponent } from './login/login.component';
import { PageIntroComponent } from './page-intro/page-intro.component';
import { UserSurveyComponent } from './user-survey/user-survey.component';
import { SurveyService } from './shared/services/survey.service';
import { JwtModule, JWT_OPTIONS, JwtHelperService } from '@auth0/angular-jwt';
import { AuthInterceptor } from "./shared/guards/auth.interceptor";
import { RegisterComponent } from './register/register.component';


@NgModule({
  declarations: [			
    AppComponent,
    PageIntroComponent,
    LoginComponent,
    UserSurveyComponent,
    RegisterComponent,
   ],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    MatFormFieldModule,
    MatIconModule,
    MatCardModule,
    MatInputModule,
    MatButtonModule,
    MatCheckboxModule,
    MatSnackBarModule,
    MatSelectModule,
    JwtModule.forRoot({
      config: {
        tokenGetter: tokenGetter,
        allowedDomains: ["https://chatbox.tpsoft.vn/","localhost:3000","localhost:4200"],
        disallowedRoutes: ["https://chatbox.tpsoft.vn/login"],
      }
    })
  ],
  providers: [
    SocketioService,
    UserService,
    AuthGuardService,
    AuthService,
    { provide: HTTP_INTERCEPTORS, useClass: AuthInterceptor, multi: true },
    SurveyService,
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }

export function tokenGetter() {
  return localStorage.getItem('token');
}
