import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';

const httpOptions = {
    headers: new HttpHeaders({
        'Content-Type': 'application/json',
        // 'Authorization': 'my-auth-token'
    })
};
@Injectable({
  providedIn: 'root'
})
export class UserService {

  baseUrl = environment.apiEndpoint + '/api/';
  
  constructor(private http: HttpClient) { }

  public getAllUser = () => {
    return this.http.get(this.baseUrl + 'Ai/Auth');
  }
  public getUserById = (userId: any) => {
    return this.http.get(this.baseUrl + 'Ai/Auth/'+ userId);
  }
  
  // tslint:disable-next-line: max-line-length
  public createUser = (data: any) => this.http.post<any>(this.baseUrl + 'Ai/Auth', data, httpOptions);
  public updateUser = (userId: any, data: any) => this.http.put<any>(this.baseUrl + 'Ai/Auth/'+ userId, data, httpOptions);
  public updatePass = (userId: any, data: any) => this.http.put<any>(this.baseUrl + 'Ai/AuthPass/'+ userId, data, httpOptions);
  public removeUserById = (userId: any) => {
    return this.http.delete<any>(this.baseUrl + 'Ai/Auth/'+ userId);
  }
}
