import { Component, OnInit } from '@angular/core';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import * as CryptoJS from 'crypto-js';
import { AuthService } from '../shared/services/auth.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
})
export class LoginComponent implements OnInit {

  hide = true;
  isLoading = false;
  loginForm = new FormGroup({
    'userName': new FormControl('', [Validators.required]),
    'pass': new FormControl('', [Validators.required, Validators.minLength(8)]),
    'isRemerber': new FormControl(false),

  });
  returnUrl: string;
  secret = "superSecret";


  get userName() { return this.loginForm.get('userName'); }
  get pass() { return this.loginForm.get('pass'); }
  get isRemerber() { return this.loginForm.get('isRemerber'); }

  constructor(
    private authService: AuthService,
    private _snackBar: MatSnackBar,
    private route: ActivatedRoute,
    private router: Router,
   
  ) {
    this.returnUrl = this.route.snapshot.queryParams['returnUrl'] || '/';
    if (this.authService.isAuthenticated()) {
      this.router.navigate([this.returnUrl]);
    }
  }

  ngOnInit(): void {
    this.checkRemerber();
    
  }

  onSubmit() {
    let data = {
      userName: this.loginForm.value.userName,
      pass: this.loginForm.value.pass
    }
    this.authService.login(data).subscribe(
      res => {
        this.isLoading = false; 
        //console.info( res['message'])
        if (res.status === 200) {
          if (res['data']) {
            localStorage.setItem('token', res['data']['access_token']);
            localStorage.setItem('refreshToken', res['data']['refresh_token']);
            localStorage.setItem('currentUser', JSON.stringify({ userName: this.loginForm.value.userName }));
            if (this.loginForm.value.isRemerber){
              localStorage.setItem('RemberUser', JSON.stringify({ userName: this.loginForm.value.userName, pass: CryptoJS.AES.encrypt(this.loginForm.value.pass, this.secret).toString(), isRemerber: this.loginForm.value.isRemerber }));
            }
              
              this.router.navigate([this.returnUrl]);
          }
          else
            this._snackBar.open('Đăng nhập không thành công', 'OK', {
              duration: 1000
            });
        }
      },
      error => {
        console.error(error)
        this.isLoading = false;
        this._snackBar.open('Đăng nhập không thành công', 'OK', {
          duration: 1000
        });
      }

    )
  }



  checkRemerber() {

    let curr = JSON.parse(localStorage.getItem('RemberUser'));
    //console.log(curr);
    if (curr) {
      this.loginForm = new FormGroup({
        'userName': new FormControl(curr.userName, [Validators.required]),
        'pass': new FormControl(CryptoJS.AES.decrypt(curr.pass, this.secret).toString(CryptoJS.enc.Utf8), [Validators.required, Validators.minLength(8)]),
        'isRemerber': new FormControl(true),
    
      });
    }
  }




}
