import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MatSnackBar } from '@angular/material/snack-bar';
import { SurveyService } from '../shared/services/survey.service';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-user-survey',
  templateUrl: './user-survey.component.html',
  styleUrls: ['./user-survey.component.css']
})
export class UserSurveyComponent implements OnInit {
  sender_id: any;
  surveyId: any;
  form: any;
  items: any;

  constructor(
    private surService: SurveyService,
    private actRoute: ActivatedRoute,
    private _snackBar: MatSnackBar
  ) {

    this.actRoute.queryParams.subscribe(params => {
      this.surveyId = params['id'];
      this.sender_id = params['sender_id'];
      
    });
   }

  ngOnInit(): void {
    this.getSurveyForm();
  }

  getSurveyForm(){
    this.surService.getById(null, this.surveyId).subscribe( res=>{
        this.form = res['data'];
        this.items = res['data'].items;
        //console.log(this.form);
    })
  }

  onSubmit(f: NgForm) {
    let s ={};
    s['surveyId'] = this.surveyId;
    s['data'] = f.value;
    s['sender_id']= this.sender_id;
    s['isDesign']= false;
    s['name']= this.form.name;
    s['items']= this.form.items;
    this.surService.createForm(null, s).subscribe(
      res => {
        if (res.status === 200) {
          this._snackBar.open('Thao tác thành công', 'OK', {
            duration: 1000
          });
        } else {
          this._snackBar.open('Thao tác không thành công', 'OK', {
            duration: 1000
          });
        }
      },
      error => {
        this._snackBar.open('Thao tác không thành công', 'OK', {
          duration: 1000
        });
      }
    );
  }

}
