import { Injectable } from '@angular/core';
import { JwtHelperService } from '@auth0/angular-jwt';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Router } from '@angular/router';
import { tap, catchError } from 'rxjs/operators';
import { throwError } from 'rxjs';

const httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
      // 'Authorization': 'my-auth-token'
    })
  };
@Injectable()
export class AuthService {
    baseUrl = environment.apiEndpoint + '/api/';

    constructor(
        public jwtHelper: JwtHelperService,
        private http: HttpClient,
        public router: Router
    ) { }
    // ...
    public isAuthenticated() {
        let token = localStorage.getItem('refreshToken');
        return !this.jwtHelper.isTokenExpired(token)
    }
    public isAuthenticatedByAdmin() {
        let token = localStorage.getItem('refreshTokenAdmin');
        return !this.jwtHelper.isTokenExpired(token)
    }

    public login = (data: any) => this.http.post<any>(this.baseUrl + 'Ai/Auth/Login', data,httpOptions);
    public loginByAdmin = (data: any) => this.http.post<any>(this.baseUrl + 'Ai/Auth/LoginByAdmin', data,httpOptions);
    public register = (data: any) => this.http.post<any>(this.baseUrl + 'Ai/Auth/register', data,httpOptions);


    public logOut() {
        localStorage.removeItem('token');
        localStorage.removeItem('refreshToken');
        this.router.navigate(['/login']);
    }

    public logOutByAdmin() {
        localStorage.removeItem('tokenAdmin');
        localStorage.removeItem('refreshTokenAdmin');
        this.router.navigate(['/login']);
    }

    public getTokenAdmin() {
        return localStorage.getItem('tokenAdmin');
    }
    public getToken() {
        return localStorage.getItem('token');
    }

    public refreshToken() {
        return this.http.post<any>(this.baseUrl + 'Ai/Auth/refresh', {
            'refreshToken': this.getRefreshToken()
        }).pipe(tap((tokens: any) => {
            localStorage.setItem('token', tokens['data']['access_token']);
            localStorage.setItem('refreshToken', tokens['data']['refresh_token']);
        }
        ), catchError(err => this.errorHandler(err, this.logOut()))
        );
    }

    
    public refreshTokenAdmin() {
        return this.http.post<any>(this.baseUrl + 'Ai/Auth/refresh', {
            'refreshTokenAdmin': this.getRefreshTokenAdmin()
        }).pipe(tap((tokens: any) => {
            localStorage.setItem('tokenAdmin', tokens['data']['access_token']);
            localStorage.setItem('refreshTokenAdmin', tokens['data']['refresh_token']);
        }
        ), catchError(err => this.errorHandler(err, this.logOut()))
        );
    }

    public decoded() {
        let token = localStorage.getItem('token');
        if (token) {
            return this.jwtHelper.decodeToken(token);
        }
        return null;
    }

    public decodedAdmin() {
        let token = localStorage.getItem('tokenAdmin');
        if (token) {
            return this.jwtHelper.decodeToken(token);
        }
        return null;
    }


    private errorHandler(error: HttpErrorResponse, logOut: any) {
        console.error(error.error.message)
        if (error.status == 403) {
            logOut;
            return throwError(error.message || "server error.");
        } else
            return throwError(error.message || "server error.");
    }

    private getRefreshToken() {
        return localStorage.getItem('refreshToken');
    }

    private getRefreshTokenAdmin() {
        return localStorage.getItem('refreshTokenAdmin');
    }
}